.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.InferenceArea {
  text-align: center;
  align-items: center;
  justify-content: center;
  display: flex;
  width: 100%;
  height: 100%;
}


.App,
#root,
body,
html {
  height: 100%;
  width: 100%;
}


.main {
  display: flex;
  flex-direction: row;
  position: absolute;
  /* transform: translate(-50%, -50%); */
  /* top: 30%;
  left: 30%;
  right: 50%; */
}

.main_centered {
  display: flex;
  flex-direction: row;
  position: absolute;
  top: 30%;
  left: 30%;
  right: 50%;
}
