hr {
  /* margin: auto 5px; */
  border: 1.5px solid rgb(150, 150, 150);
  /* padding: 0px; */
  width: 100%;
  display: inline-block;
  padding: 0 !important;
  margin: 0 !important;
}

.hr-sect {
  display: flex;
  flex-basis: 100%;
  align-items: center;
  margin: 2px 0px;
  font-size: 10px;
  cursor: move;
}

.hr-sect::before {
  content: "";
  flex-grow: 1;
  background: rgba(0, 0, 0, 1.00);
  height: 2px;
  font-size: 0px;
  line-height: 0px;
  margin: 0px 4px;
}

.hr-sect::after {
  content: "";
  flex-grow: 0;
}

.dropzone {
  background-color: #bfe4ff;
  border: dashed 3px #aaa;
  border-radius: 3px;
  /* margin: 10px auto 30px; */
  padding: 3px;
  /* width: 80%; */
  transition: background-color 0.3s;
}


.horizontal_rule {}

.rule_name {}

.separator {
  display: flex;
  align-items: center;
  text-align: right;
}

.separator::before {
  content: '';
  flex: 1;
  border-bottom: 1px solid #000;
}

.separator:not(:empty)::before {
  margin-right: .25em;
}

.hphantom {
  opacity: 0;
}

.nobreak_center,
.nobreak_center>div {
  white-space: nowrap;
  display: inline-block;
}

.small_spacer {
  width: 20px;
}

.error {
  background-color: rgba(255, 72, 26, 0.4);
}

.finished {
  background-color: rgba(0, 255, 0, 0.4);
}
